.ava {
	border-radius: 50%;
	object-fit: cover;
}

.name {
	color: #ac8f58;
	font-size: 19px;
	font-weight: 400;
	margin: 0px 0px 3px 0px;
}

.client {
	font-style: italic;
	color: #606060;
}

.info {
	align-self: center;

}

/*.body {
	max-width: 600px;
}*/
.top {
	display: grid;
	grid-template-columns: 70px auto;
	grid-gap: 20px;
	margin: 0px 0px 20px 0px;
}

.slide {
/*	background-color: #fff;
	border-radius: 7px;
	padding: 15px 80px 15px 15px;
	height: 350px;
	position: relative;*/
}


.wrapper:after {
	content: '';
	position: absolute;
	right: 0;
	top: 0;
	width: 55px;
	height: 100%;
	background-color: #ac8f58;
}

.quote {
	font-size: 50px;
	font-weight: 900;
	top: 13px;
	right: 15px;
	position: absolute;
	z-index: 3;
	font-family: Arial;
	color: #fff;
}

@media (max-width: 527px) {
	.wrapper:after {
		width: 40px;
	}
	.quote {
		font-size: 43px;
		right: 10px;
		top: 10px;
	}
}

.text {
	font-size: 19px;
	line-height: calc(28 / 19);
	font-style: italic;
	color: #444444;
}

@media (max-width: 527px) {
	.text {
		font-size: 17px;
	}
}

.wrapper {
	max-width: 500px;
/*	max-width: 450px;*/
	position: relative;
	background-color: #fff;
	border-radius: 7px;
	padding: 15px 80px 15px 15px;
	height: auto;
}

@media (max-width: 1200px) {
	.wrapper {
		max-width: 400px;
	}
}

@media (max-width: 900px) {
	.wrapper {
		max-width: 335px;
	}
}



@media (max-width: 767.98px) {
	.wrapper {
		padding: 30px 80px 30px 30px;
		border: 1px solid rgba(0, 0, 0, 0.15);
		max-width: 600px;
	}
	.slider {
		display: flex;
		justify-content: center;
	}

}

@media (max-width: 660px) {
	.wrapper {
		max-width: 450px;
	}
}

@media (max-width: 527px) {
	.wrapper {
		max-width: 400px;
		padding: 30px 55px 20px 20px;
	}
}

@media (max-width: 445px) {
	.wrapper {
		max-width: 350px;
	}
}


@media (max-width: 395px) {
	.wrapper {
		max-width: 290px;
	}
}
